import React, { useState, useEffect } from "react"
import Layout from '../../containers/layout'
import { Accordion, Box, Text, AccordionPanel } from 'grommet';
import ChartList from '../../cm-lib/reference/DetailChart.json'
import Ju from '../../cm-lib/reference/Ju.json'
import { DEAULT_DATE_FORMAT, BRANCH, DATETIME_FORMAT, DIRECTION } from '../../cm-lib/constants'
import { getZhiRunHourChart as getHourChart } from '../../cm-lib/QiMen'
import { format, parseISO, addDays, differenceInCalendarDays } from 'date-fns';
import Common from './common'
import data from '../../data/execution.json'
const queryString = require('query-string');

const isBrowser = typeof window !== `undefined`

const search = (chart, palace) => {
  const now = new Date();
  const searchEndDate = addDays(now, 5);
  const diff = differenceInCalendarDays(searchEndDate, now);
  return [...Array(diff).keys()].map(d => {
      const compareDate = addDays(now, d);
      const key = format(compareDate, DEAULT_DATE_FORMAT);
     
      return ChartList && ChartList[key]
      .reduce((a, e) => {
          const formationIndex = chart.findIndex(o => e.structure === o.structure && e.hour === o.hour)
          if(formationIndex > -1 && palace.includes(DIRECTION[chart[formationIndex].e.i])) {
              return a.concat([{
                  ...e,
                  f: { ...chart[formationIndex].e, d: DIRECTION[formationIndex] }
              }])
          }
          return a;
      }
      , [])
      .map(ee =>  {
          const hour = BRANCH.indexOf(ee.hour.substr(1, 1)) * 2;
          return { ...ee, date: `${key}T${hour.toString().padStart(2, '0')}:00:00`};
      })
  })
  .reduce((a, n) => a.concat(n), [])
}

const handleSearch = (searchFormation, palace) => {
  const found = Ju.filter(v => v.chart.find(g => g.f && g.f.indexOf(searchFormation) > -1 ))
  .map(e => ({ structure: e.structure, hour: e.hour, e: { ...e.chart.find(e => e.f === searchFormation), i: e.chart.findIndex(e => e.f === searchFormation) }}))
  const result = search(found, palace);
  const display = (v, square) => `${format(parseISO(`${v}`), DATETIME_FORMAT)} ${square.heaven} ${square.star} ${square.door} ${square.god} ${square.earth} ${DIRECTION[square.i]}`
  return result.map((v, i) => ({ ...v, display: display(v.date, v.f)}))
}

const Home = () => {

  const qs = isBrowser && queryString.parse(window && window.location.search)
  const [description, setDescription] = useState(null)
  const [ju, setJu] = useState(null)
  const [profile, setProfile] = useState(null)
  const [mode, setMode] = useState(qs.mode || 'Profile')
  const dt = isNaN(parseISO(qs.dt).getTime()) ? new Date() : parseISO(qs.dt)

  useEffect(() => {
    const foundChart = getHourChart(dt)
    setJu(foundChart.ju)
    setProfile(foundChart.profile && foundChart.profile.map(e => ({
      title: `${e.title}: ${e.label}`,
      description: e.description
    })))

  }, [])
  useEffect(() => {
    if(mode === 'Profile') {
      profile && setDescription(profile)
    } else {
      setDescription([])
    }
    
  }, [mode, profile])

  return <Layout>
    <Common
    chartType="Hour Chart"
    onClick={date => {
      const dt = format(date, 'yyyy-MM-dd HH:mm')
      isBrowser && window.location.assign(`?dt=${dt}&mode=${mode}`)
    }}
    mode={mode}
    chart={ju}
    initialDate={dt}
    heading="Qi Men Dun Jia Hour Chart"

    onSquarePressed={(e) => {
      const categories = [
        'Business',
        'Relationship',
        'Education',
        'Health',
        'Lesuire'
      ]
      const executionlist = data.filter(search => e.heaven === search.heaven && e.earth === search.earth)
      .map((e, i) => ({
        title: `${e.heaven}-${e.earth} ${categories[i]}: ${e.rating}`,
        description: e.description
      }))
      mode === 'Execution' && setDescription(executionlist)
    }}
  />
  <br />
    <Box direction="row-responsive">
      {
        [{ color: 'status-warning', mode: 'Profile' }, 
        { color: 'status-ok', mode: 'Execution' }].map(e => <Box 
          onClick={() => setMode(e.mode)}
          align="center" color="white"  background={e.mode === mode ? 'brand' : 'black'} fill="horizontal" pad="small">
          <Text color="white"> { e.mode }</Text>
        </Box>)
      }
    </Box>

  <Accordion>
    {
      description && description.map((e, i) => <AccordionPanel 
      label={e.title}>
      {
        <Box pad="medium" background="light-2">
        <Text> { ((mode !== 'Execution') || (mode === 'Execution' && qs.more)) && e.description } </Text>
          </Box>
      }
    </AccordionPanel>)
    }
  </Accordion>
  </Layout>
}

export default Home
